<template>
  <div class="Login">
    <h1>登录</h1>
    <el-form
      :model="loginForm"
      status-icon
      :rules="rules"
      ref="loginForm"
      label-width="100px"
      class="loginForm"
    >
    <el-form-item label="用户名" prop="account">
        <el-input
          v-model="loginForm.account"
          autocomplete="off"
        ></el-input>
      </el-form-item>
      <el-form-item label="密码" prop="pass">
        <el-input
          type="password"
          v-model="loginForm.pass"
          autocomplete="off"
        ></el-input>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="submitForm('loginForm')"
          >提交</el-button
        >
        <el-button @click="resetForm('loginForm')">重置</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
export default {
  name: "Login",
  props: {},
  data() {
    var validateAccount = (rule, value, callback) => {
      if (value === "") {
        callback(new Error("请输入用户名"));
      } else {
        callback();
      }
    };
    var validatePass = (rule, value, callback) => {
      if (value === "") {
        callback(new Error("请输入密码"));
      } else {
        callback();
      }
    };

    return {
      loginForm: {
        account: "",
        pass: "",
      },
      rules: {
        account: [{ validator: validateAccount, trigger: "blur" }],
        pass: [{ validator: validatePass, trigger: "blur" }],
      },
    };
  },
  methods: {
    /**
     * 提交表单
     */
    submitForm(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          this.loginRequest();
        } else {
          this.$message.error("请检查用户名和密码！");
          return false;
        }
      });
    },
    /**
     * 重置表单
     */
    resetForm(formName) {
      this.$refs[formName].resetFields();
    },
    /**
     * 登录请求
     */
    loginRequest() {
      const data = new FormData();
      data.append('account', this.loginForm.account);
      data.append('password', this.loginForm.pass);
      this.$http.post(this.$api.login, data)
      .then(res => {
          if(res.code === '200'){
            const data = res.data;
            const token = res.headers["user-session"];

            this.$storage.set("account", data.account);
            this.$storage.set("userName", data.userName);
            this.$storage.set("userRole", data.userRole);
            this.$storage.set("token", token);

            this.$message.success("登录成功！");

          } else {
            this.$message.error(res.message);
          }
      }, err => {
        this.$message.error(err.message);
      })

    }
  },
};
</script>

<style scoped lang="less">
</style>
